*, :before, :after {
  box-sizing: border-box;
}

body {
  color: #fff;
  letter-spacing: .03em;
  background-color: #ae1332;
  margin: 0;
  font-family: DM Sans, sans-serif;
  font-size: 14px;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
}

.list {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

.link {
  cursor: pointer;
  text-decoration: none;
  position: relative;
}

.container {
  width: 1280px;
  margin: 0 auto;
  padding-left: 32px;
  padding-right: 32px;
}

@media (max-width: 1179px) {
  .container {
    width: 768px;
  }
}

@media (max-width: 767px) {
  .container {
    width: 375px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

.header {
  color: #ae1332;
  background-color: #fff;
  padding: 13px 0;
}

.header-container, .menu {
  align-items: center;
  display: flex;
}

.menu-font {
  letter-spacing: -.04em;
  color: #ae1332;
  font-size: 14px;
  line-height: 1.29;
}

.menu-link {
  transition: color var(--var-1-animation), border var(--var-2-animation);
  background-color: rgba(0, 0, 0, 0);
  padding: 7px 8px;
}

.menu-link:hover, .menu-link:focus {
  color: #a4cb66;
}

.logo {
  color: #ae1332;
  text-transform: uppercase;
  letter-spacing: -.04em;
  transition: transform var(--var-1-animation);
  align-items: center;
  gap: 8px;
  font-size: .88rem;
  font-weight: 700;
  line-height: 1.29;
  display: flex;
}

.logo:hover {
  transform: scale(1.25);
}

.logo-svg {
  fill: currentColor;
}

.basket-btn, .menu-toggle {
  color: #ae1332;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
}

.color-change, .color-change {
  transition: color var(--var-1-animation);
}

.color-change:hover, .color-change:focus {
  color: var(--color-yellow);
}

.basket-icon, .burger-icon {
  width: 100%;
  height: 100%;
  fill: currentColor;
  justify-content: center;
  align-items: center;
  display: flex;
}

@media only screen and (max-width: 767px) {
  .header-container {
    justify-content: space-between;
  }

  .header-nav, .basket-btn {
    display: none;
  }
}

@media only screen and (min-width: 768px) {
  .header > .container {
    padding-left: 24px;
  }

  .header {
    padding: 11px 0;
  }

  .menu-toggle {
    display: none;
  }

  .header-nav {
    margin-right: 94px;
  }

  .basket-btn {
    margin-left: auto;
  }
}

@media screen and (min-width: 1280px) {
  .header > .container {
    padding-left: 22px;
  }

  .header-nav {
    margin-right: 322px;
  }

  .menu-link {
    padding: 7px 10px;
  }
}

@media only screen and (max-width: 767px) {
  .menu-container {
    width: 100vw;
    height: 130vh;
    z-index: 999;
    background-color: #ae1332;
    transition: transform .25s ease-in-out;
    position: fixed;
    top: 0;
    left: 0;
    overflow: scroll;
    transform: translateX(100%);
  }

  .is-open {
    transform: translateX(0);
  }

  .menu-hamburger-top {
    width: 100vw;
    height: 50px;
    background-color: #fff;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 124px;
    padding-left: 20px;
    padding-right: 20px;
    display: flex;
  }

  .top-logo {
    align-items: center;
    gap: 8px;
    display: flex;
  }

  .nav-logo {
    fill: #ae1332;
  }

  .menu-btn {
    background-color: rgba(0, 0, 0, 0);
    border: none;
  }

  .close-btn {
    width: 24px;
    height: 24px;
    color: #ae1332;
    transition: color var(--var-1-animation);
    padding: 0;
  }

  .close-btn:hover, .close-btn:focus {
    color: var(--color-yellow);
  }

  .menu-close {
    stroke: currentColor;
  }

  .menu-hamburger-title {
    text-align: center;
    letter-spacing: -.04em;
    text-transform: uppercase;
    color: #ae1332;
    align-items: center;
    font-family: inherit;
    font-size: 14px;
    font-style: normal;
    font-weight: 700;
    line-height: 1.29;
    display: flex;
  }

  .menu-hamburger-list {
    margin-left: auto;
    margin-right: auto;
    display: block;
  }

  .menu-hamburger-item {
    text-align: center;
    letter-spacing: -.04em;
    color: #fff;
    margin-bottom: 16px;
    font-family: inherit;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 1.29;
  }

  .mobile-nav__link {
    transition: color var(--var-1-animation);
    color: #fff;
  }

  .mobile-nav__link:hover, .mobile-nav__link:focus {
    color: #a4cb66;
  }

  .basket-btn-down {
    color: #fff;
    transition: color var(--var-1-animation);
    margin-left: auto;
    margin-right: auto;
    display: block;
  }

  .basket-btn-down:hover, .basket-btn-down:focus {
    color: var(--color-yellow);
  }

  .icon-basket {
    fill: currentColor;
  }

  .modal-open {
    overflow: hidden;
  }
}

@media only screen and (min-width: 768px) {
  .menu-container, .menu-hamburger-top {
    display: none;
  }
}

